.carouselContainer {
  width: 100%;
  max-width: 1200px;
  margin: 3rem auto 0;
  padding: 0 2rem;
}

.carousel {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
  border: 1px solid rgba(150, 150, 150, 0.3);
}

.slidesContainer {
  position: relative;
  width: 100%;
  height: 100%;
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.6s ease-in-out;
}

.slide.active {
  opacity: 1;
  z-index: 1;
}

.slideImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.carouselButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  border: 1px solid rgba(150, 150, 150, 0.4);
  width: 50px;
  height: 50px;
  font-size: 2rem;
  cursor: pointer;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  user-select: none;
}

.carouselButton:hover {
  background-color: rgba(0, 0, 0, 0.7);
  border-color: rgba(150, 150, 150, 0.6);
}

.carouselButton:first-of-type {
  left: 1rem;
}

.carouselButton:last-of-type {
  right: 1rem;
}

.indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1.5rem;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid rgba(150, 150, 150, 0.4);
  background-color: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.indicator:hover {
  background-color: rgba(150, 150, 150, 0.3);
  border-color: rgba(150, 150, 150, 0.6);
}

.indicator.active {
  background-color: rgba(150, 150, 150, 0.6);
  border-color: rgba(150, 150, 150, 0.8);
}

/* Responsive */
@media (max-width: 768px) {
  .carouselContainer {
    padding: 0 1rem;
    margin-top: 2rem;
  }

  .carousel {
    height: 300px;
  }

  .carouselButton {
    width: 40px;
    height: 40px;
    font-size: 1.5rem;
  }

  .carouselButton:first-of-type {
    left: 0.5rem;
  }

  .carouselButton:last-of-type {
    right: 0.5rem;
  }
}

